import * as React from "react";
import { MasonryInfiniteGrid } from "@egjs/react-infinitegrid";

import "./styles.scss";

const PhotoBoard = (props) => {
    return (
        <div className="PhotoBoardContainer">
            <MasonryInfiniteGrid className="container" gap={props.gap ? props.gap : 8}>
                {props.children.map((item, idx) => {
                    return (<div key={idx} className="PhotoBoardImg">{item}</div>);
                })}
            </MasonryInfiniteGrid>
        </div>
    );
}

export default PhotoBoard;
